* {
  margin: 0;
  padding: 0;
}
.component {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 50px;
  font-family: 'Verdana', sans-serif;
}
.no-lazy-load-component {
  background-color: rebeccapurple;
}
.lazy-load-component:nth-of-type(2) > .animated {
  background-color: orange;
}
.lazy-load-component:nth-of-type(3) > .animated {
  background-color: green;
}
.lazy-load-component:nth-of-type(4) > .animated {
  background-color: #779FA1;
}
.lazy-load-component:nth-of-type(5) > .animated {
  background-color: #C03221;
}
.lazy-load-component:nth-of-type(6) > .animated {
  background-color: #F2D0A4;
}